<template>
  <div class="whole">
    <div class="background_div">
      <div class="login_background">
        <div class="login_div">
          <div class="login_header_top_div"></div>
          <div class="login_header_div">
            <div class="login_header_logo_div">
              <img
                id="login_header_logo_img"
                class="login_header_logo_img"
                src="../assets/logo.png"
              />
            </div>
            <div
              id="header_title_text"
              class="login_header_text_div"
              data-langKey="index.header.title.text"
            >
              Hello , Welcome Back!
            </div>
          </div>
          <form
            id="login_input_div"
            class="login_input_div"
            @submit.prevent="signIn"
          >
            <div class="input_sep_line_2"></div>
            <div class="input_div">
              <input
                tabindex="2"
                name="userid"
                id="userid_inputtext"
                class="login_textfield"
                placeholder="User ID (4 - 20 characters)"
                type="text"
                spellcheck="false"
                aria-label="User ID"
                minlength="4"
                maxlength="250"
                autocomplete="off"
                v-model="user.username"
              />
            </div>
            <div class="input_sep_line_3"></div>
            <div class="input_div">
              <input
                tabindex="3"
                name="password"
                id="password_inputtext"
                class="login_textfield"
                placeholder="Password (4 - 20 characters)"
                spellcheck="false"
                aria-label="Password"
                :type="showPassword ? 'text' : 'password'"
                minlength="6"
                maxlength="16"
                required
                v-model="user.password"
              />
              <i
                class="fa eye password-icon"
                @click="showPassword = !showPassword"
                :class="{
                  'fa-eye-slash': showPassword,
                  'fa-eye': !showPassword
                }"
              ></i>
            </div>
            <div class="input_sep_line_4"></div>
            <div class="login_message_div" v-if="isSigningIn">
              <span class="line" id="line1"></span>
              <span class="line" id="line2"></span>
              <span class="line" id="line3"></span>
              sign in your account now
            </div>
            <div id="error_message" v-if="errorMessage">{{ errorMessage }}</div>

            <div class="login_button_div">
              <div class="input_div button_div_first_margin_top">
                <button
                  id="normal_login_btn"
                  data-step-idx="1"
                  @click="handleClick"
                >
                  <div class="login_text_div">
                    <span
                      class="login_text"
                      data-langKey="index.button.login.text"
                    >
                      Sign In
                    </span>
                  </div>
                </button>
              </div>
            </div>
          </form>
          <div class="login_bottom_div"></div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import axios from "axios";
import useJwt from "../composables/useJwt";

export default {
  data() {
    return {
      user: {
        username: "",
        password: "",
      },
      showPassword: false,
      isSigningIn: false,
      errorMessage: "",
    };
  },
  setup() {
    const { setToken } = useJwt();
    return {
      setToken,
    };
  },
  methods: {
    async signIn() {
      this.isSigningIn = true;
      const loginRequest = {
        username: this.user.username,
        password: this.user.password,
      };

      try {
        const response = await axios.post("/api/login", loginRequest);
        if (response.status === 200) {
          console.log(response.data);
          this.setToken(response.data.token);  // 假設後端回傳的 token 在 response.data.token
          this.$router.push("/dashboard/products");
        } else {
          this.errorMessage = "Login failed. Please check your credentials.";
        }
      } catch (error) {
        console.error("Login failed:", error);
        this.errorMessage = error.response ? error.response.data : "Login failed. Please check your credentials.";
      } finally {
        this.isSigningIn = false;
      }
    },
  },
};

</script>

